.App {
  height: 100%;
  display: table;
  table-layout: fixed;
  width: 100vw;
}


.App >  div:nth-child(2) 
{
  margin: auto;
  max-width: 1000px;
}

.low
{
  width: calc(33.33% - 3em);
}

.medium
{
  width: calc(66.66% - 3em);
}

.large
{
  width:  calc(100% - 3em);
}

@media (max-width: 768px) {
  .low , .medium{
    width:  calc(100% - 3em);
  }
}

@media (max-width: calc(1000px + 1em)) {
  .App >  div:nth-child(2) 
  {
    margin: 1em;
  }
}