.article
{
  display: inline-block;
  background: grey;
  background-position: center;
  text-align: left;
  padding: 1em;
  position: relative;
  color: white;
  margin: 0.5em;
  transition: transform 0.3s;
  text-decoration: none;
}

.listArticles
{
  display: flex;
  flex-wrap:wrap;
}

.article::before
{
  content: "";
  background-color: rgba(0, 0, 32, 0.75);
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  z-index: 1;
  opacity: 0.25;
}

.article > *{
  position: relative;
  z-index: 2; /* Positionne le contenu au-dessus du fond semi-transparent */
}

.article:hover {
  transform: scale(1.02); /* Optionnel : effet d'agrandissement */
}

.title
{
  text-transform: uppercase;
  margin-top: 0px;
  text-decoration: underline;
  overflow-wrap: break-word;
}

.article > p 
{
  opacity: 0;
}

.article > p ,.article::before {
   /* Masquer le texte par défaut */
  transition: opacity 0.3s ease; /* Transition pour l'apparition du texte */
}

.article:hover > p ,.article:hover::before{
  opacity: 1; /* Afficher le texte au survol */
}

.technoList {
  margin-top: 10px;
  display: flex;
  flex-wrap: wrap;
}

.technoItem {
  background-color: #ffffff;
  border-radius: 10px;
  padding: 5px 10px;
  margin-right: 5px;
  margin-bottom: 5px;
  font-size: 0.9rem;
  color: #333;
}