/* src/components/Header.module.css */

.header {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #333;
  padding: 10px 20px;
  color: white;
  top: 0px;
  left: 0px;
  right: 0px;
  position: fixed;
  width: auto;
  z-index: 9999;
}

.logo ,.logo > * {
  height: 2em;
}

.nav {
  display: flex;
}

.nav ul {
  list-style: none;
  display: flex;
  margin: 0;
  padding: 0;
}

.nav li {
  margin-left: 20px;
}

.navLink {
  color: white;
  text-decoration: none;
}

.navLink:hover {
  text-decoration: underline;
}
