.btnTop {
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  background-color: #555;
  color: #fff;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 5px;
  cursor: pointer;
  display: none;
  transition: background-color 0.3s;
  z-index: 2;
}
.btnTop:hover {
  background-color: #333;
}