/* ArticlePage.module.css */

.content {
  display: flex;
  flex-wrap: wrap;
}

.content > p {
  margin: 0;
  width: 50%;
}

.line {
  margin: 1em 0;
}

.title {
  text-transform: uppercase;
  margin: 1em 0 0 0;
  text-decoration: underline;
  overflow-wrap: break-word;
}

.img {
  display: block;
  width: 50%;
  margin: auto;
  min-width: 50%;
  max-height: 250px;
  object-fit: contain;
}

.technoList {
  margin-top: 10px;
  display: flex;
  flex-wrap: wrap;
}

.technoItem {
  background-color: #333333;
  border-radius: 10px;
  padding: 5px 10px;
  margin-right: 5px;
  margin-bottom: 5px;
  font-size: 0.9rem;
  color: #ffffff;
}

.navigation {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.navButton {
  background-color: #555;
  color: white;
  margin: 1em;
  padding: 1em 2em;
  text-decoration: none;
  border-radius: 5px;
  transition: background-color 0.3s;
}

.navButton:hover {
  background-color: #333;
}

@media (max-width: 768px) {
  .img {
    width: 100%;
  }

  .content > p {
    width: 100%;
  }
}
