/* Art.module.css */
.gallery {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  justify-content: space-between;
}

.galleryItem {
  flex: 1 1 calc(33.333% - 10px);
  overflow: hidden;
  
}

.galleryItem img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 8px;
  cursor: pointer;
  aspect-ratio: 1;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.largeImage {
  max-width: 80%;
  max-height: 80%;
  border-radius: 8px;
}

.arrowLeft,
.arrowRight {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  font-size: 2rem;
  background: none;
  border: none;
  color: white;
  cursor: pointer;
  padding: 10px;
}

.arrowLeft {
  left: 20px;
}

.arrowRight {
  right: 20px;
}
